import React,{useRef,useEffect,Fragment,useState} from 'react'
import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls'
import {TransformControls} from 'three/examples/jsm/controls/TransformControls'
import {EXRLoader} from 'three/examples/jsm/loaders/EXRLoader'
import * as THREE from 'three'
import _ from 'lodash'
import InfiniteGridHelper from '../../libs/THREE-InfiniteGridHelper'

import './SimRobot.scss'
import Sixi2 from '../../model/Sixi2'
import {initPoseToImg} from '../PoseToImg/PoseToImg'
import {objLoaderAsync,setupRenderer} from '../../three-js-helper'
import SegmentControls from '../../utilities/SegmentControls/SegmentControls'
import {currentState} from '../../reducers/mainReducer'
import {dispatch} from '../../contexts/MainContext'

export let canvas
export let renderer
export let scene
export let camera
export let controls
export const section3dObjs={}
export const section3dObjects={}
export let cameraPos

export let yPos
export let yLen
export let zuLen
export let vFromY
export let eaLen
export let yzInitAngle

export let wristAffector3dObject
export let wristAffectorControls

export let endAffector3dObject
export let endAffectorStart3dObject
export let endAffectorControls
export let endAffectorOffset=new THREE.Vector3(12.5+5.423,0,0)

export const loadingManager=new THREE.LoadingManager()
export const exrLoader=new EXRLoader(loadingManager)
export const textureLoader=new THREE.TextureLoader(loadingManager)

export const yellowMatcap=exrLoader.load(require('../../assets/imgs/skintone-yellow.exr'))
// export const grayMatcap=exrLoader.load(require('../../assets/imgs/skintone-gray.exr'))
// export const grayMatcap=textureLoader.load(require('../../assets/imgs/6f106fee8cd42335efe16cf4f7250ecf7c284a83.png'))

export const grayMatcap=textureLoader.load(require('../../assets/imgs/d2drhlh-0c1dc8e8-310b-4f13-b1ed-596c78153401-gray.jpg'))

export const yellowMaterial=new THREE.MeshMatcapMaterial({color: new THREE.Color('hsl(50,100%,25%)'),matcap: yellowMatcap})
export const silverMaterial=new THREE.MeshMatcapMaterial({color: new THREE.Color('hsl(0,0%,25%)'),matcap: grayMatcap})
export const blackMaterial=new THREE.MeshMatcapMaterial({color: new THREE.Color('hsl(0,0%,2%)'),matcap: grayMatcap})

// export const yellowMaterial=new THREE.MeshMatcapMaterial({color: new THREE.Color('hsl(0,0%,100%)'),matcap: grayMatcap})
// export const silverMaterial=new THREE.MeshMatcapMaterial({color: new THREE.Color('hsl(0,0%,10%)'),matcap: grayMatcap})
// export const blackMaterial=new THREE.MeshMatcapMaterial({color: new THREE.Color('hsl(0,0%,2%)'),matcap: grayMatcap})

export const objNames=['1_Anchor.obj',...Object.values(Sixi2).flatMap(o=>o.objs)].filter(o=>o)
export const objLoadPromise=Promise.all(objNames.map(o=>objLoaderAsync(require('../../assets/objs/'+o)))).then(o=>{
	o.forEach((o,i)=>section3dObjs[objNames[i]]=o)
})

export default function SimRobot(){
	canvas=useRef()
	useEffect(componentDidMount,[])
	const [endAffectorMode,setEndAffectorMode]=useState(0)

	return (
		<Fragment>
			<canvas id="SimRobot" ref={canvas}></canvas>
			<div id="IKControls" className="row spc-12">
				<div className="my-0 ml-0 button-icon" onClick={()=>controls.object.position.set(100,100,100)}>
					<div className="fa fa-repeat text-muted" style={{transform: 'rotateY(180deg)'}}/>
					<div>Reset View</div>
				</div>
				<div className="col"/>
				<div className="h7">End Affector Control Mode</div>
				<SegmentControls
					className="m-2"
					style={{width: '100px'}}
					value={endAffectorMode}
					onChange={changeEndEffectorMode}
				>
					<Fragment>Move</Fragment>
					<Fragment>Rotate</Fragment>
				</SegmentControls>
			</div>
		</Fragment>
	)

	function componentDidMount(){
		(async ()=>{
			renderer=new THREE.WebGLRenderer({
				antialias:             true,
				canvas:                canvas.current,
				alpha:                 true,
				preserveDrawingBuffer: true,
			})

			scene=new THREE.Scene()
			camera=new THREE.PerspectiveCamera()
			camera.position.set(100,100,100)
			camera.lookAt(0,30,0)
			controls=new OrbitControls(camera,renderer.domElement)
			controls.enableDamping=true
			controls.enablePan=false
			controls.dampingFactor=.1
			controls.rotateSpeed=.5
			controls.maxPolarAngle=Math.PI/2
			controls.target.set(0,30,0)
			setupThisRenderer()
			window.addEventListener('resize',setupThisRenderer,true)

			const pointLight=new THREE.PointLight()
			pointLight.position.set(0,100,0)
			scene.add(pointLight)
			const ambientLight=new THREE.AmbientLight()
			ambientLight.intensity=.25
			scene.add(ambientLight)

			await objLoadPromise
			// console.log(scene,section3dObjs)
			initPoseToImg(scene.clone(),_.mapValues(section3dObjs,o=>o.clone()))

			postObjLoad(scene,section3dObjs,section3dObjects,renderer)

			wristAffector3dObject=new THREE.Object3D()
			scene.add(wristAffector3dObject)

			wristAffectorControls=new TransformControls(camera,renderer.domElement)
			wristAffectorControls.addEventListener('dragging-changed',({value})=>controls.enabled=!value)
			wristAffectorControls.setSpace('local')
			wristAffectorControls.attach(wristAffector3dObject)
			scene.add(wristAffectorControls)
			wristAffectorControls.scale.set(0,0,0)

			endAffector3dObject=new THREE.Mesh(
				new THREE.SphereBufferGeometry(1,32,32),
				new THREE.MeshLambertMaterial()
			)
			scene.add(endAffector3dObject)

			endAffectorStart3dObject=new THREE.Object3D()
			endAffectorStart3dObject.position.sub(endAffectorOffset)
			endAffector3dObject.add(endAffectorStart3dObject)

			endAffectorControls=new TransformControls(camera,renderer.domElement)
			endAffectorControls.addEventListener('dragging-changed',({value})=>controls.enabled=!value)
			endAffectorControls.setSpace('local')
			endAffectorControls.attach(endAffector3dObject)
			scene.add(endAffectorControls)

			scene.updateMatrixWorld()
			let pos=new THREE.Vector3()
			section3dObjects['v'].getWorldPosition(pos)
			// section3dObjects['t'].inner.getWorldPosition(pos)
			// pos.add(new THREE.Vector3(12.5,1,0))
			wristAffector3dObject.position.set(...pos.toArray())

			endAffector3dObject.position.add(pos)
			endAffector3dObject.position.add(endAffectorOffset)

			// console.log(section3dObjects['x'].getWorldPosition())
			// console.log(section3dObjects['y'].getWorldPosition())
			// console.log(section3dObjects['z'].getWorldPosition())
			// console.log(section3dObjects['u'].getWorldPosition())
			// console.log(section3dObjects['v'].getWorldPosition())
			// console.log(section3dObjects['w'].getWorldPosition())
			// console.log(section3dObjects['t'].getWorldPosition())

			yPos=new THREE.Vector3()
			section3dObjects['y'].getWorldPosition(yPos)
			yLen=section3dObjects['z'].position.length()
			zuLen=new THREE.Vector3().addVectors(section3dObjects['u'].position,section3dObjects['v'].position).length()
			vFromY=new THREE.Vector3()
			vFromY.subVectors(wristAffector3dObject.position,yPos)
			eaLen=vFromY.length()
			yzInitAngle=angleFromSSSTriangle(eaLen,zuLen,yLen)*THREE.Math.RAD2DEG

			wristAffectorControls.addEventListener('change',()=>{
				if(wristAffectorControls.dragging){
					const xyz=wristIK(wristAffector3dObject.position)
					rotateSections(xyz)
					scene.updateMatrixWorld()
					let pos=new THREE.Vector3()
					section3dObjects['v'].getWorldPosition(pos)
					if(pos.distanceTo(wristAffector3dObject.position).toFixed(2)==='0.00')
						dispatch(['SET_SIM_ANGLES',{
							...currentState.simPose,
							...xyz,
						}])
					else{
						rotateSections(currentState.simPose)
						scene.updateMatrixWorld()
						pos=new THREE.Vector3()
						section3dObjects['v'].getWorldPosition(pos)
					}
					wristAffector3dObject.position.copy(pos)
				}
			})

			const xAxis=new THREE.Vector3(1,0,0)
			const p=new THREE.Object3D()
			endAffectorControls.addEventListener('change',()=>{
				if(endAffectorControls.dragging){
					scene.updateMatrixWorld()
					const eaPos=new THREE.Vector3()
					endAffector3dObject.getWorldPosition(eaPos)
					const easPos=new THREE.Vector3()
					endAffectorStart3dObject.getWorldPosition(easPos)

					const xyz=wristIK(easPos)
					rotateSections(xyz)
					// NOTE: Maybe move EA to V

					scene.updateMatrixWorld()
					p.position.copy(eaPos)

					const uv={}
					section3dObjects['u'].attach(p)
					uv.u=
						(Math.atan(p.position.z/p.position.y)*THREE.Math.RAD2DEG || 0)
						*(p.position.x>=0?1:-1)
						*(p.position.y>=0?-1:1)
					section3dObjects['v'].attach(p)
					uv.v=
						p.position.angleTo(xAxis)*THREE.Math.RAD2DEG
						*(p.position.y>=0?-1:1)
					rotateSections(uv)
					section3dObjects['v'].remove(p)

					scene.updateMatrixWorld()
					const q1=new THREE.Quaternion()
					section3dObjects['w'].getWorldQuaternion(q1)
					const q2=new THREE.Quaternion()
					endAffectorStart3dObject.getWorldQuaternion(q2)
					const w=
						q1.angleTo(q2)*THREE.Math.RAD2DEG
						*(easPos.z>=0?-1:1)

					dispatch(['SET_SIM_ANGLES',{
						...currentState.simPose,
						...xyz,
						...uv,
						w
					}])
				}
			})

			scene.add(new InfiniteGridHelper(10,100,new THREE.Color('hsl(0,0%,25%)'),camera.far/2))
			scene.fog=new THREE.Fog(new THREE.Color('hsl(230,10%,10%)'),camera.near,camera.far/2)
			render()
		})()
	}

	function changeEndEffectorMode(i){
		switch(i){
			case 0:
				endAffectorControls.setMode('translate')
				break
			case 1:
				endAffectorControls.setMode('rotate')
				break
			default:
		}
		setEndAffectorMode(i)
	}
}

export function setupThisRenderer(){
	setupRenderer(canvas.current.offsetWidth,canvas.current.offsetHeight,renderer,camera)
}

export function postObjLoad(scene,section3dObjs,section3dObjects,renderer){
	_.forIn(Sixi2,(s,i)=>section3dObjects[i]=datatTo3dObject(s,i,section3dObjs,section3dObjects))

	const section=section3dObjects['t'].inner
	const leftFork=section.children[1].inner
	const rightFork=section.children[2].inner
	section3dObjects['t'].rotateSection=function(offset){
		// NOTE: Verify and remove .7
		const factor=(offset-Sixi2['t'].angles.max)/Sixi2['t'].angles.max*.7
		leftFork.position.z=factor*Sixi2['t_l'].position[2]
		rightFork.position.z=factor*Sixi2['t_r'].position[2]
	}
	scene.add(section3dObjs['1_Anchor.obj'])
	scene.add(section3dObjects['x'])

	section3dObjs['1_Anchor.obj'].children[0].material=yellowMaterial
	section3dObjs['1_Anchor.obj'].children[1].material=blackMaterial

	section3dObjs['2_Shoulder.obj'].children[0].material=blackMaterial
	section3dObjs['2_Shoulder.obj'].children[1].material=silverMaterial
	section3dObjs['2_Shoulder.obj'].children[2].material=blackMaterial
	section3dObjs['2_Shoulder.obj'].children[3].material=yellowMaterial

	section3dObjs['3_Bicep.obj'].children[0].material=yellowMaterial
	section3dObjs['3_Bicep.obj'].children[1].material=silverMaterial
	section3dObjs['3_Bicep.obj'].children[2].material=blackMaterial
	section3dObjs['3_Bicep.obj'].children[3].material=yellowMaterial
	section3dObjs['3_Bicep.obj'].children[4].material=yellowMaterial
	section3dObjs['3_Bicep.obj'].children[5].material=yellowMaterial
	section3dObjs['3_Bicep.obj'].children[6].material=yellowMaterial

	section3dObjs['4_Forearm.obj'].children[ 0].material=yellowMaterial
	section3dObjs['4_Forearm.obj'].children[ 1].material=yellowMaterial
	section3dObjs['4_Forearm.obj'].children[ 2].material=silverMaterial
	section3dObjs['4_Forearm.obj'].children[ 3].material=blackMaterial
	section3dObjs['4_Forearm.obj'].children[ 4].material=yellowMaterial
	section3dObjs['4_Forearm.obj'].children[ 5].material=yellowMaterial
	section3dObjs['4_Forearm.obj'].children[ 6].material=yellowMaterial
	section3dObjs['4_Forearm.obj'].children[ 7].material=blackMaterial
	section3dObjs['4_Forearm.obj'].children[ 8].material=blackMaterial
	section3dObjs['4_Forearm.obj'].children[ 9].material=blackMaterial
	section3dObjs['4_Forearm.obj'].children[10].material=silverMaterial
	section3dObjs['4_Forearm.obj'].children[11].material=silverMaterial
	section3dObjs['4_Forearm.obj'].children[12].material=silverMaterial
	section3dObjs['4_Forearm.obj'].children[13].material=blackMaterial
	section3dObjs['4_Forearm.obj'].children[14].material=blackMaterial
	section3dObjs['4_Forearm.obj'].children[15].material=blackMaterial
	section3dObjs['4_Forearm.obj'].children[16].material=yellowMaterial
	section3dObjs['4_Forearm.obj'].children[17].material=silverMaterial
	section3dObjs['4_Forearm.obj'].children[18].material=blackMaterial
	section3dObjs['4_Forearm.obj'].children[19].material=blackMaterial
	section3dObjs['4_Forearm.obj'].children[20].material=blackMaterial
	section3dObjs['4_Forearm.obj'].children[21].material=yellowMaterial

	section3dObjs['5_TuningFork.obj'].children[0].material=yellowMaterial
	section3dObjs['6_PicassoBox.obj'].children[0].material=yellowMaterial
	section3dObjs['7_Hand.obj'].children[0].material=yellowMaterial

	section3dObjs['8_ClampBase.obj'].children[0].material=blackMaterial
	section3dObjs['8_ClampBase.obj'].children[1].material=blackMaterial
	section3dObjs['8_ClampBase.obj'].children[2].material=yellowMaterial
	section3dObjs['8_ClampBase.obj'].children[3].material=yellowMaterial
	section3dObjs['8_ClampBase.obj'].children[4].material=blackMaterial
	section3dObjs['8_ClampBase.obj'].children[5].material=blackMaterial
	section3dObjs['8_ClampBase.obj'].children[6].material=blackMaterial
	section3dObjs['8_ClampBase.obj'].children[7].material=yellowMaterial

	section3dObjs['9_Clamp_Left_Fixed.obj'].children[0].material=yellowMaterial
	section3dObjs['9_Clamp_Right_Fixed.obj'].children[0].material=yellowMaterial

	// renderer.toneMapping=THREE.CineonToneMapping
	// renderer.toneMapping=THREE.ReinhardToneMapping
	renderer.toneMapping=THREE.ACESFilmicToneMapping
	renderer.toneMappingExposure=4
	renderer.outputEncoding=THREE.sRGBEncoding
}

export function datatTo3dObject(data,name,section3dObjs,section3dObjects){
	const axis=new THREE.Vector3(...data.axis)
	const sectionOuter=new THREE.Object3D()
	sectionOuter.name='section-'+name
	sectionOuter.position.set(...data.position)
	sectionOuter.setRotationFromAxisAngle(axis,data.angles.home*THREE.Math.DEG2RAD)

	const sectionInner=new THREE.Object3D()
	sectionOuter.add(sectionInner)
	sectionInner.name='section-'+name+'-inner'
	data.objs.map(o=>sectionInner.add(section3dObjs[o]))

	sectionOuter.sectionName=name
	sectionOuter.inner=sectionInner
	sectionOuter.axis=axis
	sectionOuter.rotateSection=function(angle){
		angle=THREE.Math.clamp(angle,data.angles.min,data.angles.max)
		if(sectionOuter.sectionAngle===angle) return
		sectionOuter.sectionAngle=angle
		sectionInner.setRotationFromAxisAngle(axis,-angle*THREE.Math.DEG2RAD)
	}
	sectionOuter.rotateSection(data.angles.home)

	if(data.parent)
		section3dObjects[data.parent].inner.add(sectionOuter)
	return sectionOuter
}

function render(){
	requestAnimationFrame(render)
	controls.update()
	cameraPos=controls.object.position
	renderer.render(scene,camera)
}

export function poseLerp(a,b,alpha){
	return _.mapValues(a,(v,i)=>THREE.Math.lerp(a[i],b[i],alpha))
}

// TODO: Add to helper lib
function angleFromSSSTriangle(a,b,c){
	return Math.acos((b**2+c**2-a**2)/(2*b*c))
}

function wristIK(pos){
	pos=vFromY.subVectors(pos,yPos)
	const wristLen=pos.length()

	let xAngle=-Math.atan(pos.z/pos.x)*THREE.Math.RAD2DEG
	if(pos.x<0)
		if(pos.z>0) xAngle=xAngle-180
		else xAngle=180+xAngle

	const wristXZ=pos.clone().setY(0).length()
	const alpha=Math.atan(pos.y/wristXZ)*THREE.Math.RAD2DEG
	const beta=angleFromSSSTriangle(zuLen,yLen,wristLen)*THREE.Math.RAD2DEG || 0

	const zAngle=angleFromSSSTriangle(wristLen,zuLen,yLen)*THREE.Math.RAD2DEG || 180
	return {
		x: xAngle,
		y: -alpha-beta,
		z: yzInitAngle-zAngle,
	}
}

export function rotateSections(data){
	data=JSON.parse(JSON.stringify(data))
	for(let i in data){
		data[i]=THREE.Math.clamp(data[i],Sixi2[i].angles.min,Sixi2[i].angles.max)
		section3dObjects[i].rotateSection(data[i])
	}
	return data
}

